.auction-sec{
    background-color: #121127;
    padding: 75px 0px;
}
.auction-row{
    padding: 40px 0px;
    align-items: center;
}
.aut-left{
    background-color: #151232;
    padding: 25px;
    border-radius: 22px;
    opacity: 1;
}
.aut-head {
    text-align: center;
}
.aut-head h4 {
    color: #fff;
    font-size: 16px;
    line-height: 24px;
    text-transform: capitalize;
}
.auction-grid {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 25px;
    padding-top: 25px;
    padding-bottom: 25px;
}
.auction-img1 img {
    width: 100%;
    height: 100%;
}

/* auction right start */
.aut-right {
    padding: 20px;
}
.auction-profile {
    text-align: center;
}
.auction-profile img {    
    width: 100%;
    height: 100%;
}
.auction-ct {
    display: flex;
    color: #fff;
    grid-gap: 15px;

}
.auction-ct  .form-control {
    display: block;
    width: 100%;
    padding: 9px 10px;
    font-weight: 400;
    color: #212529;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 20px;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    font-style: italic;
    font-size: 12px;
    line-height: 24px;
    text-transform: capitalize;
}
.actprize-btn1 h5 {
    font-size: 14px;
    line-height: 24px;
    text-transform: uppercase;
    color: #F9F9F9;
}
.actprize-btn1 .form-select {
    display: block;
    width: 100%;
    padding: 0.375rem 0.25rem 0.6rem 0.75rem;
    -moz-padding-start: calc(0.75rem - 3px);
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #fff;   
    background-repeat: no-repeat;
    background-position: right 0.75rem center;
    background-size: 16px 12px;
    border: 1px solid #ced4da;
    border-radius: 20px;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}