.duelacept-sec {
    background-color: #121127;
    padding: 75px 0px;
}
.duelat-main {
    padding: 40px 0px;
    align-items: center;
    max-width: 778px;
    margin: auto;
}
.dA-profile .clearfix {
    display: flex;
    background-color: #282454;
    border-radius: 16px;
    opacity: 1;
    padding: 10px;
}
.duelA-center {
    text-align: center;
}
.dA-slider {
    padding-top: 30px;
    padding-bottom: 20px;
}
.dA-slider img {
    border-radius: 20px;
    opacity: 1;
}
.carousel-control-prev-icon {
    background-image: url(../images/Chevron-icon.png);
}
.carousel-control-next-icon {
    background-image: url(../images/Chevron-lefticon.png);
}
.dA-left {
    position: relative;
}
.select-btn {
    text-align: center;
    position: absolute;
    bottom: 100px;
    left: 32%;
}
.dA-slider .carousel-indicators {
    display: none;
}
.dA-left .select-btn .hero-btn {
    font-size: 14px;
    background-color: #151232;
    padding: 6px 29px 6px 50px;
    border-radius: 25px;
    border: 1px solid #151232;
    color: #fff;
    text-transform: uppercase;
    line-height: 30px;
    position: relative;
}
.dA-left .select-btn .hero-btn span {
    background: #4f6bbc;
    padding: 9px 16px;
    border-radius: 25px;
    font-size: 18px;
    line-height: 24px;
    position: absolute;
    top: 0;
    left: 0;
    font-weight: 500;
}




