.duelchalenge-sec{
    background-color: #121127;
    padding: 100px 0px;
}

.duelchalenge-sec .nav-tabs .nav-link {
    margin-bottom: -1px;
    background: 0 0;
    border: 1px solid transparent;
    border-top-left-radius: 0.25rem;
    border-top-right-radius: 0.25rem;
    padding: 5px 40px;
    background-color: #282454;
    border-radius: 16px;
    margin: 10px 0px;
    font-size: 16px;
    line-height: 25px;
    text-transform: capitalize;
    color: #fff;
}
.tabct-main {
    max-width: 990px;
    margin: auto;
    padding-top: 35px;
}
.tab-left {   
    border-radius: 16px;
}
.tab-left .dule-img1 {
    text-align: center;
}
.tab-left .dule-img1:nth-child(4) {
    /* border: 1px dashed #4A6BBC; */
    border-radius: 20px;
    opacity: 1;
}

.row.tabChallenge-main {
    padding: 30px 0px;
}

.dule-img1:nth-child(4) {
    border-radius: 20px;
    opacity: 1;
}
.dchallenge-rt-1{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 10px;
    background-color: #282054;
    padding: 25px 20px;
    border-radius: 20px;
}
.dchallenge-rt-1 .dule-img1 img {
    width: 85%;
}
.dchallenge-rt-1 .dule-img1 img:hover {
    transform: scale(.9);
    transition: ease-in-out 0.5s;
}





/* tab right css */

.tab-left .float-end {
    float: right!important;
    color: #fff;
    font-family: 'Poppins';
}
 .clearfix {
    display: flex;
    background-color: #282254;
    padding: 20px;
    border-radius: 16px;
}
.challenge-list {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    grid-gap: 36px;
    padding: 70px 0px 70px 0px;
    
}
.won-title h4 {
    font-size: 14px;
    line-height: 24px;
    color: #00C853;
    text-transform: uppercase;
    font-family: 'Poppins', sans-serif;

}
.won-title span {
    font-size: 56px;
    line-height: 41px;
    color: #00C853;
    font-weight: 400;
    font-family: 'Poppins', sans-serif;
}
.lost-title h4 {
    font-size: 14px;
    line-height: 24px;
    color: #D82626;
    text-transform: uppercase;
    font-family: 'Poppins', sans-serif;

}
.lost-title span {
    font-size: 56px;
    line-height: 41px;
    color: #D82626;
    font-weight: 400;
    font-family: 'Poppins', sans-serif;
}

.tab-right .duel-form .form-control {
    display: block;
    width: 100%;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #fff;
    background-color: #282254;
    background-clip: padding-box;
    border: 1px solid #282254;
    appearance: none;
    border-radius: 20px;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    font-style: italic;
    font-size: 14px;
}

/* plus button */


.button {
    cursor: pointer;
    text-align: center;
    width:150px;
    height:150px
  }
  
  /* .icon-plus {
    position: relative;
    border-radius: 10%;
    color: #282054;
    height: 100px;
    width: 100px;
    line-height: 90px;
    font-size: 96px;
    text-align: center;
    transition: all .3s;
    z-index: 1;
  
    position: relative;
    left: 48px;
   top: 50px;
  } */
  
  .icon-plus:before {
    background-color: #43387f;
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    width: 20%;
    transform: translateX(-50%);
    transition: all .3s;
    z-index: -1;

  }
  
  .icon-plus:after {
    background-color: #43387f;
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    height: 20%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    transition: all .3s;
    z-index: -1;
  }
  
  .icon-plus:hover {
    color: #555;
  }
  
  .icon-plus:hover:before {
    border-radius: 10%;
    width: 100%;
  }
  
  .icon-plus:hover:after {
    border-radius: 10%;
    height: 100%;
  }